export class PlThemeColors {
  static pure = [
    '#F5B805', '#FF9300', '#EC7658', '#897AFB', '#5E6EEB',
    '#4D97FF', '#01BBC7', '#3DDB88', '#A4EA4D', '#93A3D3',
    '#000000', '#ffffff'
  ]
  static gradient = [
    '#FBDD0C,#F5B805',
    '#FFB046,#FF9300',
    '#FF9579,#EC7658',
    '#8B99FF,#897AFB',
    '#7F8DFF,#5E6EEB',
    '#79B1FF,#4D97FF',
    '#00DBE9,#01BBC7',
    '#46FA9B,#3DDB88',
    '#C1FF73,#A4EA4D',
    '#B5C7FD,#93A3D3',
  ]
  static SetColors (pure: string[], gradient: string[]) {
    this.SetPure(pure)
    this.SetGradient(gradient)
  }
  static SetPure (pure: string[]) {
    if (pure && pure.length > 0) this.pure = pure
  }
  static SetGradient (gradient: string[]) {
    if (gradient && gradient.length > 0) this.gradient = gradient
  }

}
