import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { map } from 'rxjs/operators';
import { addBaseUrl, DATA_PREFIX } from '../const';
import { Agg, AggResult, Dataset, DatasetDto, Dimension, DpValue, RuntimeAggOption } from '../types/data.type';

export const datasetUrls = {
  GET_BY_ID: `${DATA_PREFIX}/DataSetService/find/`,
  CREATE: `${DATA_PREFIX}/DataSetService/create`,
  IMPORT_QO: `${DATA_PREFIX}/DataSetService/importQO`,
  DELETE: `${DATA_PREFIX}/DataSetService/delete`,
  GET_ALL: `${DATA_PREFIX}/DataSetService/findAll`, // 500
  GET_RAW_DIMS_BY_DATASET: `${DATA_PREFIX}/DataResultSetService/tryGetDimensions`,
  GET_RAW_DATA_BY_DATASET: `${DATA_PREFIX}/DataResultSetService/getResultSet`,
  UPDATE: `${DATA_PREFIX}/DataSetService/update`,
}
export const aggUrls = {
  SAVE: `${DATA_PREFIX}/DataSetService/saveAggConfig`,
  GET_BY_RESOURCE: `${DATA_PREFIX}/DataSetService/findAggConfigByResource`,
}
export const dataResultSetUrls = {
  GET_BY_AGG_ID: `${DATA_PREFIX}/DataResultSetService/getAggResultSet/{aggId}`, // ?
  GET_BY_AGG: `${DATA_PREFIX}/DataResultSetService/getAggResultSetForTest`,
  GET_BY_DATASET: `${DATA_PREFIX}/DataResultSetService/getResultSet`, // åå§æ°æ®ï¼ç¨ä¸å°
  GET_BY_RESOURCE: `${DATA_PREFIX}/DataResultSetService/getResultSetByResource`, //
}
addBaseUrl(datasetUrls)
addBaseUrl(aggUrls)
addBaseUrl(dataResultSetUrls)

@Injectable({
  providedIn: 'root'
})
export class DatasetRepo {

  constructor (
    private http: HttpClient,
  ) { }

  getRawDimsByDataset (datasetDto: DatasetDto) {
    return this.http
      .post<Dimension[]>(datasetUrls.GET_RAW_DIMS_BY_DATASET, datasetDto)
  }
  getRawColsNDataByDataset (datasetDto: DatasetDto) {
    return this.http
      .post<{columns: string[], data:any[]}>(datasetUrls.GET_RAW_DATA_BY_DATASET, datasetDto)
  }
  getDatasets () {
    return this.http
      .get<DatasetDto[]>(datasetUrls.GET_ALL)
      .pipe(map(datasetDtos => datasetDtos.map(Dataset.fromDto)))
  }
  getDatasetById (id: string) {
    return this.http
      .get<DatasetDto>(datasetUrls.GET_BY_ID + id)
      .pipe(map(Dataset.fromDto))
  }
  saveDataset (datasetDto: DatasetDto) {
    return this.http.post<DatasetDto>(
      datasetDto.id ? datasetUrls.UPDATE : datasetUrls.CREATE,
      // Dataset.toDto(dataset)
      datasetDto
    ).pipe(map(Dataset.fromDto))
    // TODO:
    // , this.success()
  }
  importQO (ids: string[]) {
    return this.http.post(datasetUrls.IMPORT_QO, ids)
  }
  deleteDataset (datasetId: string) {
    return this.http
      .post<any>(datasetUrls.DELETE, null, { params: { id: datasetId } })
      // TODO:
      // .pipe(this.success())
  }

  /** agg */
  getAggByPortletId (portletId: string) {
    return this.http.get<Agg>(aggUrls.GET_BY_RESOURCE, {
      params: {
        resource: 'portlet',
        resourceId: portletId
      }
    }).pipe(map(Agg.fromDto))
  }
  saveAgg (agg: Agg) {
    return this.http
      .post<Agg>(aggUrls.SAVE, Agg.toDto(agg))
  }

  /** result */
  getAggResultByAgg (
    agg: Agg,
    dpValues: DpValue[] = [],
    runtimeAgg?: RuntimeAggOption
  ) {
    const body: any = {
      aggDataDto: Agg.toDto(agg),
      aggregation: RuntimeAggOption.toDto(runtimeAgg)
    }
    // dataParam ä¸º [] æå¡ç«¯ä¼æ¥éï¼è¥æ²¡æç´æ¥ä¸ä¼ æ­¤åæ°ã
    if (dpValues && dpValues.length > 0) body.dataParam = dpValues.map(DpValue.toDto)
    return this.http.post<AggResult>(dataResultSetUrls.GET_BY_AGG, body)
  }
  getResultByPortletId (
    portletId: string,
    dpValues: DpValue[] = [],
    filter = '""',
    runtimeAgg?: RuntimeAggOption
    ) {

    const body: any = {
      filter,
      aggregation: RuntimeAggOption.toDto(runtimeAgg)
    }
    // dataParam ä¸º [] æå¡ç«¯ä¼æ¥éï¼è¥æ²¡æç´æ¥ä¸ä¼ æ­¤åæ°ã
    if (dpValues && dpValues.length > 0) body.dataParam = dpValues.map(DpValue.toDto)

    return this.http.post<AggResult>(
      dataResultSetUrls.GET_BY_RESOURCE,
      body,
      {
        headers: new HttpHeaders({'Content-Type': 'application/json'}),
        params: {
          resourceType: 'portlet',
          resourceId: portletId
        }
      }
    )
  }
}
