import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { of } from 'rxjs';
import { catchError, map } from 'rxjs/operators';
import { addBaseUrl } from '../const';
import { Role } from '../types/igix.type';
import { EppPortlet } from '../types/portlet.type';
import { convertWidget } from './widget.repo';

export const portletUrls = {
  GET_BY_ID: `portlet/`,
  GET_USER_ALL: `portlet/all`,
  SAVE: `portlet/`,
  GET_BY_MODULE_ID: `portlet/findByModuleId`,
  DELETE_BY_ID: `portlet/`,
  GET_PAGE:`page/findByPortletId`
}
export const portletOps = {
  SAVE: `portletop/save?portletId=`,
  GET_BY_PORTLET_ID: `portletop/getallpagerole?portletId=`
}
addBaseUrl(portletUrls)
addBaseUrl(portletOps)

@Injectable({
  providedIn: 'root'
})
export class PortletRepo {

  constructor (
    private http: HttpClient,
  ) { }

  getPageByPortletId(portletId:string){
    return this.http.get<number>(portletUrls.GET_PAGE,{
      params:{portletId}
    })
  }

  getPortlets () {
    return this.http.get<EppPortlet[]>(portletUrls.GET_USER_ALL).pipe(
      map(portlets => portlets.map(convertPortlet))
    )
  }
  getPortletsByModuleId (moduleId?: string) {
    return this.http.get<EppPortlet[]>(
      portletUrls.GET_BY_MODULE_ID,
      { params: moduleId ? { moduleId } : null }
    ).pipe(map(portlets => portlets.map(convertPortlet)))
  }
  getPortletById (id: string) {
    // return this.storage.getPortletById(id)
    return this.http.get<EppPortlet>(portletUrls.GET_BY_ID + id).pipe(
      map(convertPortlet),
      // å¦æè¯·æ±åºéï¼ä¼è¿åä¸ä¸ª nullï¼èä¸åæ
      // catchError(err => {
      //   const p = new EppPortlet(null)
      //   p.id = id
      //   p.name = ''
      //   return of(p)
      // })
    )
  }
  savePortlet (portlet: EppPortlet) {
    const serverPortlet = convertPortletInverse(portlet)
    return portlet.id
      ? this.http.post<EppPortlet>(portletUrls.SAVE, serverPortlet)
      : this.http.put<EppPortlet>(portletUrls.SAVE, serverPortlet)
  }

  /** portlet op */
  getPortletOps (portletId: string) {
    return this.http.get<Role[]>(portletOps.GET_BY_PORTLET_ID + portletId)
  }
  savePortletOps (portletId: string, opIds: string[]) {
    return this.http.post(portletOps.SAVE + portletId, opIds)
  }
  deletePortletById (portletId: string) {
    return this.http.delete(portletUrls.DELETE_BY_ID + portletId)
  }
}

/** portlet åç«¯ -> åç«¯, åç«¯ -> åç«¯ è½¬æ¢ */
function convertPortlet (serverPortlet) {
  serverPortlet.widget = convertWidget(serverPortlet.widget)
  if (serverPortlet.propValues) serverPortlet.propValues = JSON.parse(serverPortlet.propValues)
  return Object.assign(new EppPortlet(null), serverPortlet) as EppPortlet
}
function convertPortletInverse (portlet: EppPortlet) {
  const serverPortlet = { ...portlet }
  delete serverPortlet.widget
  serverPortlet.propValues = JSON.stringify(portlet.propValues) as any
  return serverPortlet
}
