export class ArrUtil {
  static SwapByIndex (arr: any[], i, j) {
    const tmp = arr[i]
    arr[i] = arr[j]
    arr[j] = tmp
  }
  static Add (arr: any[], newItem) {
    arr.push(newItem)
  }
  static Up (arr: any[], item) {
    const i = arr.indexOf(item)
    if (i < 1) return
    ArrUtil.SwapByIndex(arr, i, i-1)
  }
  static Down (arr: any[], item) {
    const i = arr.indexOf(item)
    if (i > arr.length - 2) return
    ArrUtil.SwapByIndex(arr, i, i+1)
  }
  static Remove (arr: any[], item) {
    const i = arr.indexOf(item)
    if (i === -1) return
    arr.splice(i, 1)

    // arr = arr.filter(i => item!==i)
  }
  static Unique (arr: any[]) {
    return Array.from(new Set(arr))
  }
  // TODO: æåç´ å¯¹è±¡çæå­æ®µæåº
}