/**
 * å½¢å¦ {key={key=value, key=value}, key={}}
 * è½¬ä¸º JSON æ åæ ¼å¼
 */
export function backendString2Json (original: string) {
  let result = original.replace(/\s/g, '')    // å»ç©ºæ ¼
  result = result.replace(/=/g, ':')    // = å :
  result = result.replace(/:/g, '\":')  // : åå  "
  result = result.replace(/,/g, ',\"')  // , åå  "
  result = result.replace(/{(?=\w)/g, '{\"')  // åæ¥å­æ¯ç { åå  "
  return result
}
/**
 * ç¨åè¡¨æé ä¸æ£µæ ã
 */
export function list2Tree (list: any[], {
  parentIdKey = 'parentId',
  rootParentIdValue = null,
  idKey = 'id',
  // leafSelectableOnly = true
  // ignoreLeaf = false
  ifIgnore = (node) => false,
  // isLeaf = (node) => false,
  beforeProcess = null,
  afterProcess = null,
}) {
  function recurseAddChildren (nodes: any[], layer = 0) {
    nodes.forEach(node => {
      if (beforeProcess) beforeProcess(node)
      if (!node.layer) node.layer = layer
      node.value = node.key = node[idKey]
      node.label = node.title = node.name
      const children = list.filter(item => item[parentIdKey] === node[idKey] && !ifIgnore(item))
      if (children.length > 0) {
        node.children = children
        recurseAddChildren(node.children, layer + 1)
      } else {
        node.isLeaf = true
      }
      if (afterProcess) afterProcess(node)
    })
  }

  const tree = list.filter(item => !item[parentIdKey] || item[parentIdKey] === rootParentIdValue)
  recurseAddChildren(tree)
  return tree
}

export function uuidv4 () {
  return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, c => {
    var r = Math.random() * 16 | 0, v = c == 'x' ? r : (r & 0x3 | 0x8);
    return v.toString(16);
  });
}

export function appendScript (src: string) {
  const body = document.getElementsByTagName('body')[0];
  const script = document.createElement('script');
  script.src = src
  body.appendChild(script);
}

/**
 * å è½½ css
 * 1. é²éå¤ï¼å¦æç¸å href ç link æ ç­¾å·²å­å¨ï¼ä¸ä¼éæ°å è½½
 * 2. å id æ¿æ¢ï¼å¯ä»¥æ¿æ¢å id ç link æ ç­¾
 */
export function loadCss (href: string, eleId?: string, allowRepeat = false) {
  // é²éå¤
  if (
    !allowRepeat
    && Array.from(document.querySelectorAll('link')).some(link => link.href === href)
  ) return
  // å id æ¿æ¢
  if (eleId) {
    const themeLink = document.getElementById(eleId) as HTMLLinkElement;
    if (themeLink) {
      themeLink.href = href
      return
    }
  }
  const head = document.getElementsByTagName('head')[0];
  const style = document.createElement('link');
  if (eleId) style.id = eleId;
  style.type = 'text/css'
  style.rel = 'stylesheet';
  style.href = href;
  head.appendChild(style);
}

export function deepClone (obj) {
  return JSON.parse(JSON.stringify(obj))
}

/** ç´æ¥æ¹åæ°ç» */
export function setArrayLengthTo (arr: any[], targetLength: number, fillItem) {
  arr.length = targetLength
  for (let i = 0; i < arr.length; i++) {
    if (!arr[i]) arr[i] = deepClone(fillItem)
  }
}
