import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { map, tap } from 'rxjs/operators';
import { addBaseUrl } from '../const';
import { PageInfo, PageWithRole, SimplePage } from '../types/page.type';
import { mapEntityFeilds } from '../utils/field-mapping';

export const pageInfoUrls = {
  GET_BY_ID_MANAGE: `page/`,
  GET_BY_ID_PERSONAL: `page/pageofuser?pageId=`,
  GET_ALL_MANAGE: `page/all/mg`,
  GET_ALL_PERSONAL: `page/pagelistofuser`,
  GET_BY_ROLE: `pagerole/pagesbyrole?roleId=`,
  GET_PAGES_WITH_ROLE: `pagerole/all`,
  SET_TO_ROLE_DEFAULT: `pagerole/settingrolepageenable`, // roleId pageId
  DELETE: `page/`, // delete + id
  DELETE_MANAGE: `page/delete/mg`, // post []
  ADD: `page`,
  ADD_MANAGE: `page/add/mg`,
  UPDATE: `page`,
  UPDATE_MANAGE: `page/update/mg`,
  COPY: `page/copy`,
  COPY_MANAGE: `page/copy/mg`,
  RESET_PAGE: `page/resetsyspage?pageId=`,
  SELECT_PAGE: `page/changepageselect?pageId=`,
}
export const pageRoles = {
  SAVE: `pagerole/save?pageId=`,
  GET_BY_PAGE_ID: `pagerole/getallpagerole?pageId=`,
}
addBaseUrl(pageInfoUrls)
addBaseUrl(pageRoles)

@Injectable({
  providedIn: 'root'
})
export class PageRepo {

  constructor (
    private http: HttpClient,
  ) { }

  getPagesManage () {
    return this.http
      .get<any[]>(pageInfoUrls.GET_ALL_MANAGE)
      .pipe(map(data => data.map(convertPageInfo)))
  }
  getPagesUser () {
    return this.http
      .get<SimplePage[]>(pageInfoUrls.GET_ALL_PERSONAL)
  }
  getPagesByRole (roleId: string) {
    return this.http
      .get<PageWithRole[]>(pageInfoUrls.GET_BY_ROLE + roleId)
  }
  getPagesWithRole () {
    return this.http.get<PageWithRole[]>(pageInfoUrls.GET_PAGES_WITH_ROLE)
  }
  setRoleDefaultPage (roleId: string, pageId: string) {
    return this.http
      .post(pageInfoUrls.SET_TO_ROLE_DEFAULT, null, {
        params: { pageId, roleId }
      }).pipe(this.success())
  }
  getPageInfoById (id: string, isManage = false) {
    return this.http
      .get<PageInfo>((isManage
        ? pageInfoUrls.GET_BY_ID_MANAGE
        : pageInfoUrls.GET_BY_ID_PERSONAL)
        + id)
      .pipe(map(data => convertPageInfo(data)))
  }
  savePageInfo (pageInfo: PageInfo, isManage = false) {
    return pageInfo.id
      ? this.http.post(
        isManage ? pageInfoUrls.UPDATE_MANAGE : pageInfoUrls.UPDATE,
        convertPageInfoInverse(pageInfo)
      ).pipe(this.success())
      : this.http.put(
        isManage ? pageInfoUrls.ADD_MANAGE : pageInfoUrls.ADD,
        convertPageInfoInverse(pageInfo)
      ).pipe(this.success())
  }
  deletePageInfo (id: string) {
    return this.http
      .delete(pageInfoUrls.DELETE + id, { responseType: 'text' })
      .pipe(this.success())
  }
  deletePageInfoManage (ids: string[]) {
    return this.http
      .post(pageInfoUrls.DELETE_MANAGE, ids)
      .pipe(this.success())
  }
  copyPageInfo (ids: string[], isManage = false) {
    return this.http
      .post(isManage ? pageInfoUrls.COPY_MANAGE : pageInfoUrls.COPY, ids, { responseType: 'text' })
  }
  resetPage (pageId: string) {
    return this.http.post(pageInfoUrls.RESET_PAGE + pageId, null)
  }
  selectPage (pageId: string) {
    return this.http.post(pageInfoUrls.SELECT_PAGE + pageId, null)
  }

  // TODO: ä¸è¯¥å¨è¿ä¾èµ nzmsgï¼åºç§»å°ä½¿ç¨å¤
  private success () {
    return tap<any> (
      () => {
        // this.nzMsg.success(`æä½æåï¼`, { nzDuration: 1000 })
      }
    )
  }
  /** page roles */
  savePageRoles (pageId: string, roleIds: string[]) {
    return this.http.post(pageRoles.SAVE + pageId, roleIds)
  }
  getPageRoles (pageId: string) {
    return this.http.get<PageWithRole[]>(pageRoles.GET_BY_PAGE_ID + pageId)
  }
}

/** pageInfo åç«¯ -> åç«¯, åç«¯ -> åç«¯ è½¬æ¢ */
export function convertPageInfo (original) {
  const result: PageInfo = mapEntityFeilds(original, 'pageInfo')
  result.config = JSON.parse(result.config as any)
  return result
}
export function convertPageInfoInverse (pageInfo) {
  const result: any = mapEntityFeilds(pageInfo, 'pageInfo', true)
  // ä¿å­æ¶ï¼å é¤ pageInfo.config.layout çåç´ ç portlet
  result.config.layout = result.config.layout.map(item => {
    const { instanceId, isBAPortlet, gridsterItem, portletId, link } = item
    return { instanceId, isBAPortlet, gridsterItem, portletId, link }
  })
  result.config = JSON.stringify(result.config)
  return result
}
