export interface RuntimeAggOptionDto {
  dimensions: string[]
  indicators: {
    type: number
    name: string
  }[]
}
export class Indicator {
  dimName?: string
  aggFn?: 1 | 2 | 3 | 4 | 5 = 4
}
export class RuntimeAggOption {
  dimensions: string[] = []
  indicators: Indicator[] = [new Indicator()]
  static toDto (runtimeAgg: RuntimeAggOption) {
    if (!runtimeAgg) return
    return {
      dimensions: runtimeAgg.dimensions,
      indicators: runtimeAgg.indicators.map(ind => ({
        type: ind.aggFn,
        name: ind.dimName
      }))
    } as RuntimeAggOptionDto
  }
}
