import { EventEmitter, Injectable } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { AppOptions } from '../public-api';

const WINDOW_RTF_KEY = 'gspframeworkService'

export interface RtfTabChangeEvent {
  appEntrance: string
  appId: string
  appType: string
  funcId: string
  tabId: string
  tabName: string
}

@Injectable()
export class RtfUtilService {

  constructor (
    private translate: TranslateService
  ) { }

  // TODO: queryParamArray æ¯ä¸ª array å°±è¡
  openMenu (funcId: string, queryParamArray?: Map<string, string>, tabId?: string, isReload?: boolean) {
    (window as any).gspframeworkService.rtf.func.openMenu({
      appType: 'menu',
      funcId, tabId, isReload,
      appId: '', appEntrance: '',
      queryStringParams: new Map(queryParamArray)
    } as AppOptions)
  }
  closeMenu (funcId: string, tabId?: string) {
    (window as any).gspframeworkService.rtf.func.close({
      appType: 'menu',
      funcId, tabId,
      appId: '', appEntrance: ''
    } as AppOptions)
  }
  openApp (appId: string) {
    (window as any).gspframeworkService.rtf.func.openMenu({
      appType: 'app',
      funcId: '',
      appId,
      appEntrance: '',
    } as AppOptions);
  }
  public openUserInfoMenu () {
    const { rtf } = window[WINDOW_RTF_KEY]
    const frmEventSvc = rtf.frmEvent
    const sessionSvc = rtf.adapter.adatperSerice().session
    this.translate.get('userInfo').subscribe(txt => {
      frmEventSvc.eventFire(`farrisapp-click`, {
        active: false,
        code: 'userInfo',
        src: '/platform/runtime/sys/web/userinfo/index.html',
        sessionid: sessionSvc.getUserSessionId(),
        id: 'userinfo',
        appType: '',
        FuncName: txt
      })
    })
  }
  closeCurRtfTab (route: ActivatedRoute) {
    const { tabId, funcId } = route.snapshot.queryParams
    this.closeMenu(funcId, tabId)
  }

  onRtfTabChangeToMe (route: ActivatedRoute, condition?: (toTabId: string, curTabId: string) => boolean) {
    const onRtfTabChangeToMe$ = new EventEmitter()
    const { tabId: curTabId } = route.snapshot.queryParams
    onRtfTabChange().subscribe((e: RtfTabChangeEvent) => {
      if (!condition) condition = (pToTabId: string, pCurTabId: string) => pToTabId === pCurTabId
      if (condition(e.tabId, curTabId)) {
        onRtfTabChangeToMe$.emit(e)
      }
    })
    return onRtfTabChangeToMe$
  }
}

const onRtfTabChange$ = new EventEmitter<RtfTabChangeEvent>()
let funcSwitchEventRegistered = false
/** ç¨å½æ°æ¯ä¸ºäºå»¶è¿æ³¨åäºä»¶ï¼å¦å rtf service å¯è½è¿æ²¡æå¨ window ä¸ */
export function onRtfTabChange () {
  if (!funcSwitchEventRegistered && window[WINDOW_RTF_KEY]) {
    funcSwitchEventRegistered = true
    window[WINDOW_RTF_KEY].rtf.frmEvent.eventListener('funcSwitchEvent', (e: RtfTabChangeEvent) => {
      onRtfTabChange$.emit(e)
    })
  }
  return onRtfTabChange$
}
