import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { addBaseUrl, DATA_PREFIX } from '../const';
import { DataProvider, Datasrc, ProviderParam } from '../types/data.type';

export const datasrcUrls = {
  GET_BY_ID: `${DATA_PREFIX}/DatasourceService/find/`, // ?
  CREATE: `${DATA_PREFIX}/DatasourceService/create`, // 500
  DELETE: `${DATA_PREFIX}/DatasourceService/delete`, // ?
  GET_ALL: `${DATA_PREFIX}/DatasourceService/findAll`, // 500
  UPDATE: `${DATA_PREFIX}/DatasourceService/update`, // ?
  TRY: `${DATA_PREFIX}/DatasourceService/try`,
  GET_PARAMS_BY_PROVIDER_NAME: `${DATA_PREFIX}/DataService/getDatasourceParameters/`,
  GET_ALL_PROVIDERS: `${DATA_PREFIX}/DataService/getProvicerNames`,
}
addBaseUrl(datasrcUrls)

@Injectable({
  providedIn: 'root'
})
export class DatasrcRepo {

  constructor (
    private http: HttpClient,
  ) { }

  getDatasrcList () {
    return this.http.post<Datasrc[]>(datasrcUrls.GET_ALL, null)
  }
  getDatasrcById (id: string) {
    return this.http
      .get<Datasrc>(datasrcUrls.GET_BY_ID + id)
  }
  saveDatasrc (datasrc: Datasrc) {
    if (datasrc.id) {
      return this.http.post<Datasrc>(datasrcUrls.UPDATE, datasrc)
      // TODO: ç§»èµ°
      // .pipe(this.success())
    } else {
      return this.http.post<Datasrc>(datasrcUrls.CREATE, datasrc)
      // TODO: ç§»èµ°
      // .pipe(this.success())
    }
  }
  deleteDatasrc (id: string) {
    return this.http.post<string>(datasrcUrls.DELETE, null, {
      params: { id }
    })
    // TODO: ç§»èµ°
    // .pipe(this.success())
  }
  tryDatasrc (datasrc: Datasrc): Observable<string> {
    return this.http.post(datasrcUrls.TRY, datasrc, { responseType: 'text' })
    // TODO: ç§»èµ°
    // .pipe(tap(
    //   () => this.nzMsg.success('æµè¯æåï¼'),
    //   () => this.nzMsg.error('æµè¯å¤±è´¥ï¼')
    // ))
  }
  getProviders () {
    return this.http.get<DataProvider[]>(datasrcUrls.GET_ALL_PROVIDERS)
  }
  getParamsByProviderName (providerName: string) {
    return this.http.get<ProviderParam[]>(datasrcUrls.GET_PARAMS_BY_PROVIDER_NAME + providerName)
  }
}
