export const LS_LOAD_LOCAL_ASSET = 'LS_LOAD_LOCAL_ASSET'
const loadLocalAsset = localStorage.getItem(LS_LOAD_LOCAL_ASSET)
export const IGIX_HOME_PATH = loadLocalAsset ? `` : `/platform/runtime/sys/web/home/`

export const BASE_URL = `/api/runtime/epp/v1.0/`
export function addBaseUrl (node) {
  Object.keys(node).forEach(key => {
    if (typeof node[key] === 'string') {
      node[key] = BASE_URL + node[key]
    } else {
      addBaseUrl(node[key])
    }
  })
}
