@Injectable({
  providedIn: 'root'
})
export class IgixRepo {

  constructor (
    private http: HttpClient,
  ) { }

  getBOTree (layer = Infinity) {
    return this.http.get<BO[]>(igixUrls.bo.GET_ALL).pipe(map(boList => {
      return list2Tree(boList, {
        parentIdKey: 'parentID',
        ifIgnore: item => item.layer > layer
      })
    }))
  }
  getOpsByBOId (boId: string): Observable<any> {
    return this.http.get<any[]>(igixUrls.funcOperations.GET_BY_BO_ID + boId)
  }
  /** role, role group */
  getRoleGroupTree () {
    return this.http.get<RoleGroup[]>(igixUrls.roleGroup.GET_ALL).pipe(map(roleGroups => {
      return list2Tree(roleGroups, {})
    }))
  }
  getRolesByRoleGroupId (groupId: string) {
    return this.http.get<any>(igixUrls.role.GET, {
      params: {
        param: JSON.stringify({ groupId })
      }
    }).pipe(map(res => res.data))
  }
  getAllRoles () {
    return this.http.get<any>(igixUrls.role.GET).pipe(map(res => res.data))
  }

  funcTree12
  /** è·åé¨ä»¶åè¡¨éè¦çäºçº§ bo tree */
  getFuncTree12 () {
    if (this.funcTree12) return of(this.funcTree12)
    return this.getAllFuncs()
      .pipe(
        map(funcs => {
          funcs = funcs.filter(func => parseInt(func.layer) < 3)
          this.funcTree12 = list2Tree(funcs, {
            rootParentIdValue: '0',
            parentIdKey: 'parentId',
          })
          return this.funcTree12
        })
      )
  }
  getQoDatasetDetailsByModuleId (moduleId: string) {
    return this.http.put<any>(igixUrls.qoDataset.GET_DETAIL_QUERY, {}, {
      params: {
        entityFilter: JSON.stringify({
          FilterConditions: [{
            FilterField: 'moduleID',
            Compare: 0,
            Value: moduleId,
            Relation: 0
          }],
          SortConditions: [],
          IsUsePagination: true,
          Pagination: {
            PageIndex: 1,
            PageSize: 20, // TODO:
            PageCount: 0,
            TotalCount: 0
          }
        })
      }
    // }, null).pipe(map(res => res.returnValue))
    }).pipe(map(res => res.returnValue.result))
  }
  getQoDatasetDetailById (id: string) {
    return this.http.put<any>(igixUrls.qoDataset.GET_DETAIL_BY_ID + id, {}).pipe(map(res => res.returnValue))
  }
  /**
   * è·å igix ç¨æ·ä¿¡æ¯
   * @param reload é»è®¤ä¸ºåï¼ä¸åååºè¯·æ±ï¼ä½¿ç¨å·²ç¼å­çç»æï¼çåéæ°è¯·æ±
   */
  userInfoPromise: Promise<UserInfo>
  getUserInfo (reload = false) {
    if (reload) this.userInfoPromise = null
    if (!this.userInfoPromise) this.userInfoPromise = this.http
      .get<UserInfo>(igixUrls.userInfo.GET)
      .toPromise()
    return from(this.userInfoPromise)
  }
  getUserAvatar (reload = false) {
    return this.getUserInfo(reload).pipe(map(userInfo => {
      if (userInfo && userInfo.userSetting && userInfo.userSetting.imgblob) return userInfo.userSetting.imgblob
      return null
    }))
  }
  getAllFuncs () {
    return this.http.get<RtfFunc[]>(igixUrls.func.GET_ALL)
  }
  getBAPortletsAll () {
    return this.http
      .get<any>(igixUrls.ba.GET_ALL_PORTLETS,
        // {
        // responseType: 'text'
        // }
      ).pipe(
        // map(res => JSON.parse(res)),
        map(res => res.data
          // .filter(p => p.tab === 1)
          .map(p => Object.assign(new BAPortlet(), p))))
  }
  // getBAPortletById () {
  //   return this.http
  //     .get<any>(igixUrls.ba.GET_ALL_PORTLETS)
  //     .pipe(map(res => res.categoryList.map(p => Object.assign(new BAPortlet(), p))[0]))
  // }

  getLangsPromise: Promise<Lang[]>
  getLangs () {
    if (!this.getLangsPromise) this.getLangsPromise = this.http
      .get<Lang[]>(igixUrls.langs.GET_ALL)
      .pipe(map(rtfLangs => rtfLangs.map(rtfLang => new Lang(rtfLang.code, rtfLang.name))))
      .toPromise()
    return this.getLangsPromise
  }
}
